/* global google */
/* eslint-disable brace-style */
import { h, Component } from 'preact';

const mapStyle = [
	{
		featureType: 'water',
		stylers: [{ color: '#d5d5d5' }]
	},
	{
		featureType: 'landscape',
		stylers: [{ color: '#ffffff' }]
	},
	{
		featureType: 'road',
		stylers: [{ weight: 0.5 }, { color: '#c4c4c4' }]
	},
	{
		featureType: 'poi',
		stylers: [{ visibility: 'off' }]
	},
	{
		featureType: 'poi.medical',
		elementType: 'labels',
		stylers: [{ color: '#808080' }, { weight: 1 }, { visibility: 'on' }]
	},
	{},
	{
		featureType: 'road.highway',
		stylers: [{ color: '#aaaaaa' }, { weight: 0.3 }, { visibility: 'off' }]
	},
	{
		featureType: 'administrative.locality',
		elementType: 'labels.text.fill',
		stylers: [{ visibility: 'on' }, { color: '#737373' }]
	},
	{
		featureType: 'administrative.country',
		elementType: 'geometry.stroke',
		stylers: [{ weight: 1 }, { color: '#969696' }]
	},
	{
		featureType: 'administrative.country',
		elementType: 'labels.text.fill',
		stylers: [{ color: '#808080' }]
	}
];
const small = 5;
const medium = 11;
const large = 21;
function arrowSizeAt(count) {
	let rval = 35;
	if (count >= large) {
		rval = 75;
	} else if (count >= medium) {
		rval = 50;
	} else if (count >= small) {
		rval = 40;
	}
	return rval;
}

export default class Map extends Component {
	openBubble = false;
	listners = [];
	markers = [];
	addMarkers = data => {
		const bounds = new google.maps.LatLngBounds();
		const bubbles = [];
		const { listners, markers, map } = this;
		const { api, mode } = this.props;
		for (let i = 0; i < data.length; i++) {
			const datum = data[i];
			let className = 'tiny-circle';
			if (datum.active >= large) {
				className = 'large-circle';
			} else if (datum.active >= medium) {
				className = 'medium-circle';
			} else if (datum.active >= small) {
				className = 'small-circle';
			} else if (datum.active === 0) {
				className = 'zero-circle';
			}
			const loc = new google.maps.LatLng(datum.lat, datum.lng);
			bounds.extend(loc);
			const marker = new window.RichMarker({
				position: loc,
				map,
				flat: true,
				dragganle: false,
				content:
					'<div class="map-circle ' +
					className +
					'">' +
					datum.active +
					'</div>'
			});

			markers.push(marker);

			const evt = google.maps.event.addListener(
				marker,
				'click',
				function() {
					if (this.openBubble) {
						this.openBubble.close();
					}
					const curIdx = markers.indexOf(this);

					// bubbles on demand
					if (!bubbles[curIdx]) {
						const curDatum = api[mode][curIdx];
						const bubble = new window.InfoBubble({
							map,
							content:
								mode === 'countries'
									? `<h3>${datum.percentage}%<h3>
								<h4>of global infections</h4>
								<h2>${datum.country}</h2>
								<ul class="bubble-list">
									<li>Total ${datum.total}</li>
									<li>${datum.cured} recovered</li>
									<li>${datum.died} died</li>
								</ul>`
									: `<h3>${datum.percentage}%<h3>
								<h4>of global infections</h4>
								<h2>${datum.city}</h2>
								<ul class="bubble-list">
									<li>Total ${datum.total}</li>
									<li>${datum.cured} recovered</li>
									<li>${datum.died} died</li>
								</ul>`,
							backgroundColor: '#fff',
							borderRadius: 300,
							backgroundClassName: 'map-bubble',
							arrowStyle: 7,
							minWidth: 274,
							minHeight: 274,
							maxWidth: 274,
							maxHeight: 274,
							arrowSize: arrowSizeAt(curDatum.active),
							shadowStyle: 0,
							borderColor: '#DE3C2D',
							borderWidth: 3
						});
						bubbles[curIdx] = bubble;
					}
					bubbles[curIdx].open(map, markers[curIdx]);
					this.openBubble = bubbles[curIdx];
				}
			);
			listners.push(evt);
		}
		this.map.fitBounds(bounds);
	};
	showMarkers = () => {
		const viewBounds = this.map.getBounds();
		const { markers } = this;
		if (markers && markers.length > 0) {
			for (let i = markers.length - 1; i >= 0; i--) {
				if (!viewBounds.contains(markers[i].getPosition())) {
					if (markers[i].getMap()) {
						markers[i].setMap(null);
					}
				} else if (!markers[i].getMap()) {
					markers[i].setMap(this.map);
				}
			}
		}
	};
	setupMap = el => {
		if (el) {
			const $script = require('scriptjs');
			const load = () =>
				$script(
					'https://maps.googleapis.com/maps/api/js?key=AIzaSyDH7YkLcwjNohpFCMGSHAqCaxA_k4G2sP8',
					() => {
						require('../utils/infobubble');
						require('../utils/richmarker');
						const mapOptions = {
							center: new google.maps.LatLng(21.597, 39.144),
							zoom: 8,
							styles: mapStyle
						};
						this.map = new google.maps.Map(el, mapOptions);
						google.maps.event.addListener(
							this.map,
							'idle',
							this.showMarkers
						);
						this.addMarkers(
							this.props.api[this.props.mode].map(row => ({
								...row,
								...this.props.api.all[row.id]
							}))
						);
						window.addEventListener('orientationchange', e => {
							if (
								screen.orientation === 90 ||
								screen.orientation === -90
							) {
								setTimeout(function() {
									this.map.fitBounds(this.map.getBounds());
								}, 10);
							}
						});
					}
				);
			if (document.readyState === 'comlete') {
				load();
			} else {
				document.addEventListener('DOMContentLoaded', () => load());
			}
		}
	};
	clear() {
		for (let i = 0; i < this.markers.length; i++) {
			this.markers[i].setMap(null);
			google.maps.event.removeListener(this.listners[i]);
		}
		this.markers.length = 0;
		this.listners.length = 0;
	}
	componentDidUpdate(prevProps) {
		if (prevProps.mode !== this.props.mode) {
			this.clear();
			if (this.props.mode === 'countries') {
				this.addMarkers(this.props.api[this.props.mode]);
			} else {
				this.addMarkers(
					this.props.api[this.props.mode].map(row => ({
						...row,
						...this.props.api.all[row.id]
					}))
				);
			}
			this.showMarkers();
		} else if (
			prevProps.curIdx !== this.props.curIdx &&
			this.props.curIdx != null
		) {
			this.map.panTo(this.markers[this.props.curIdx].getPosition());
			if (this.props.mode === 'countries') {
				this.map.setZoom(5);
			} else {
				this.map.setZoom(11);
			}
		}
	}
	render() {
		return (
			<section class="map">
				<figure ref={this.setupMap} />
			</section>
		);
	}
}



