import { h } from 'preact';

export default ({ api, mode, onClick }) => (
	<ul>
		{api[mode].map(row => ({ ...row, ...api.all[row.id] })).map(({ active, city, country, total, cured, note, died }, idx) => (
			<li class="scroll-item" data-idx={idx} role="link" onClick={() => onClick(idx)}>
				<section class="scroll-item-box">
					<div class="item-count">
						<span>{active}</span>
					</div>
					<div class="item-detail">
						{(() => {
							if (city) {
								return [
									<h3>{city}</h3>,
									<cite>{country}</cite>
								];
							}
							return <h3>{country}</h3>;
						})()}
						<p>
							Total {total}{' '}
							{cured ? ` | ${cured} recovered` : ''}{' '}
							{died ? ` | ${died} died` : ''}
						</p>
						{note ? <p class="news">{note}</p> : ''}
					</div>
				</section>
			</li>
		))}
	</ul>
);



