import { h } from 'preact';


export default ({ world }) => (
	<div class="stats">
		<div class="stat-item">
			<p id="stat-total" class="stat-number">
				{world.total}
			</p>
			<p class="stat-label">Infected</p>
		</div>
		<div class="stat-item">
			<p id="stat-died" class="stat-number">
				{world.died}
			</p>
			<p class="stat-label">Died</p>
		</div>
		<div class="stat-item">
			<p id="stat-risk" class="stat-number">
				{world.risk}%
			</p>
			<p class="stat-label">Case fatality rate</p>
		</div>
	</div>
);



