import { h, Component } from 'preact';

export default class extends Component {
	load = (el) => {
		const $script = require('scriptjs');
		if (el) {
			const load = () => $script('//platform.twitter.com/widgets.js');
			if (document.readyState === 'comlete') {
				load();
			} else {
				document.addEventListener('DOMContentLoaded', () => load());
			}
		}
	};
	shouldComponentUpdate() {
		return false;
	}
	render() {
		return (
			<div class="footer" ref={this.load}>
				<div role="button" onClick={() => this.props.onClick()}>
					About
				</div>
				<div class="tweet tweet-btn">
					<a
						href="https://twitter.com/coronamap"
						class="twitter-follow-button"
						data-show-count="false"
						data-dnt="true"
					>
						Follow @coronamap
					</a>
				</div>
				<div class="tweet">
					<a
						href="https://twitter.com/share"
						class="twitter-share-button"
						data-url="http://coronamap.com"
						data-text="Follow the latest numbers on #MERS corona virus"
						data-related="coronamap"
						data-via="coronamap"
						data-count="none"
					>
						Tweet
					</a>
				</div>
			</div>
		);
	}
}



