import './style';
import { Component } from 'preact';
import distanceInWords from 'date-fns/distance_in_words';
import Stats from './components/stats';
import List from './components/list';
import Footer from './components/footer';
import Map from './components/map';
import api from './api.json';

export default class App extends Component {
	state = {
		mode: 'cities',
		curIdx: null,
		showAbout: false
	};
	render(props, state) {
		return (
			<article>
				<section class="main">
					<div class="scroll-list" role="navigation">
						<h1 class="logo">MERS Corona Map</h1>
						<div class="scroll-head">
							<div class="update">
								Updated{' '}
								{distanceInWords(
									api.world.modified,
									new Date()
								)}
							</div>
						</div>
						<Stats world={api.world} />
						<div class="partition">
							<div
								class={`map-country${
									state.mode === 'countries' ? ' picked' : ''
								}`}
								onClick={() => {
									this.setState({
										mode: 'countries',
										curIdx: null
									});
								}}
							>
								Countries
							</div>
							<div
								class={`map-city${
									state.mode === 'cities' ? ' picked' : ''
								}`}
								onClick={() => {
									this.setState({
										mode: 'cities',
										curIdx: null
									});
								}}
							>
								Top cities
							</div>
							<div
								class={`map-new${
									state.mode === 'new' ? ' picked' : ''
								}`}
								onClick={() => {
									this.setState({
										mode: 'new',
										curIdx: null
									});
								}}
							>
								Latest cities
							</div>
						</div>
						<section class="details">
							<strong>
								All data are by WHO and Health Ministries
							</strong>
							<br />Colored circles indicate current active cases
						</section>
						<List
							api={api}
							mode={state.mode}
							onClick={curIdx => {
								this.setState({ curIdx });
							}}
						/>
						<div class="rotate">
							Rotate your device to see the map
						</div>
					</div>
					<Footer
						onClick={() => {
							this.setState({
								showAbout: true
							});
						}}
					/>
				</section>
				<Map
					api={api}
					mode={this.state.mode}
					curIdx={this.state.curIdx}
				/>
				<section
					class={`about${
						this.state.showAbout ? ' about-visible' : ''
					}`}
					style={{ display: this.state.showAbout ? 'block' : '' }}
				>
					<ul class="about-list">
						<li class="about-item">
							<span>Data</span> @iwail
						</li>
						<li class="about-item">
							<span>Development</span> @salzhrani
						</li>
						<li class="about-item">
							<span>Assistance</span> @11bushra11
						</li>
						<li class="about-item">
							<span>Design</span> @bandar
						</li>
						<div class="about-close" onClick={() => {
							this.setState({showAbout: false})
						}} />
					</ul>
				</section>
			</article>
		);
	}
}



